<template>
  <div>
    <!-- Header -->
    <div class="pop-top">
      <p class="title">개인정보 수집 · 이용 동의</p>
      <a class="btn-close" v-on:click="$emit('popclose')"></a>
    </div>
    <!-- Content -->
    <div id="content" class="container pop-wrap">
      <p class="mt15">본인은 「신용정보의 이용 및 보호에 관한 법률」 제15조, 제33조, 「정보통신망 이용촉진 및 정보보호 등에 관한 법률」 제22조, 「개인정보 보호법」 제15조 등의 관련 법규에 따라 귀사가 아래와 같은 내용으로 본인의 개인정보를 수집•이용하는 것에 동의합니다.</p>
      <div>
        <h3>제 1 조 (개인정보의 수집•이용 목적)</h3>
        <p class="mt15">회원제 서비스 제공을 위한 본인확인, 신용조회회사에 대한 회원의 신용정보보고서 제공 요청, 신용관리/등급, 신용리포트, 신용정보현황, 신용거래현황 검색•조회 등을 통한 신용관리서비스 제공, 금융기관 별 대출/소득정보 검색•조회•안내 등을 통한 부채관리서비스 제공, 비금융정보(소득금액증명정보 등)의 신용등급 평가 반영, 안정적인 서비스 운영 및 제공을 위한 안전성 테스트 및 모니터링 등의 목적</p>
      </div>
      <div>
        <h3>제 2 조 (개인정보의 수집항목)</h3>
        <p class="mt15">이름, 성별, 생년월일, 휴대전화번호, PIN, 이메일, 신용등급 및 신용점수, 신용정보 변동여부 및 변동내역, 타기관에 의한 신용정보 조회내역, 카드보유현황, 대출현황, 연체현황, 연대보증 현황, 중복가입확인정보(DI), 연계정보(CI), 내/외국인 정보, 회원 본인 명의의 계좌번호, 잔액 및 거래내역, 회원 본인 명의의 증권 계좌 정보, 건강보험료 납부내역, 소득정보</p>
      </div>
      <div>
        <h3>제3조 (개인정보의 수집항목)</h3>
        <p class="mt15">서비스이용계약 종료시(관계법령에 보존기간 등이 별도로 규정되어 있는 경우에는 그에 따름)</p>
      </div>
    </div>
    <!-- //Content -->
  </div>
</template>

<script>
export default {
  name: "Terms4"
};
</script>

